import React from "react";
import { hostURL, FullBaseURL } from "../../package.json";

function LoadHTMLContent(url, isClick = false) {
  // const [loading, setLoading] = useState(true);
  url = isClick ? url : FullBaseURL + url;
  // setLoading(true);
  httpGet(url).then((resp) => {
    let content = resp.slice(
      resp.indexOf('<div class="container body-content">'),
      resp.indexOf("<footer>")
    );
    content =
      '<link href="/web-site-api/Content/bootstrap.css" rel="stylesheet"/>' +
      content;
    let ifr = document.createElement("iframe");
    ifr.setAttribute("id", "api-content");
    let div = document.getElementById("div-content"); // width: 95% !imporant
    if (div) div.innerHTML = "";
    div.appendChild(ifr);
    document.getElementById("api-content").outerHTML = content;

    document
      .getElementsByClassName("body-content")[0]
      ?.addEventListener("click", (e) => {
        onClickIfr(e);
      });
    // setLoading(false);
  });
}

async function httpGet(theUrl) {
  var xmlHttp = new XMLHttpRequest();
  xmlHttp.open("GET", theUrl, false);
  await xmlHttp.send(null);
  return xmlHttp.responseText;
  // return "<h2>1234</h2";
}

function onClickIfr(e) {
  if (e.target.getAttribute("type") === "submit") {
    let form = e.target.form;
    var myWindow = window.open("", "MsgWindow", "width=200,height=100");
    form.setAttribute("action", hostURL + form.getAttribute("action"));
    myWindow.document.body.appendChild(form);
    form.submit();
    setTimeout(() => {
      myWindow.close();
      LoadHTMLContent(e.target.form.action, true);
    }, 250);
  } else {
    e.preventDefault();
    let url = e.target.href;
    if (url === undefined) return;
    LoadHTMLContent(url, true);
  }
}

export const Print = (view) => {
  setTimeout(() => {
    LoadHTMLContent(view);
  }, 200);
  return (
    <>
      <h2 className={"content-block"}>{view}</h2>
      <div id="div-content"></div>
    </>
  );
};
