import { withNavigationWatcher } from "./contexts/navigation";
import { HomePage, Software } from "./pages";
import { Aptitudes } from "./pages";
import { Studies } from "./pages";
import { Trayectoria } from "./pages/";

// add push api's whith api true
const routes = [
  {
    path: "/home",
    component: HomePage,
  },
  {
    path: "/studies",
    component: Studies,
  },
  {
    path: "/trayectoria",
    component: Trayectoria,
  },
  {
    path: "/aptitudes/software",
    component: Software,
  },
  {
    path: "/admin/aptitudes",
    component: Aptitudes,
    api: true,
  },
];

export default routes.map((route) => {
  return {
    ...route,
    component: withNavigationWatcher(route.component),
  };
});
